.items {
  display: block;
  position: relative;
  width: 100%;
  float: left;
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    width: 95%;
    max-width: 1200px;
    min-width: 300px;
    margin-left: auto;
    margin-right: auto;
    text-align: inherit;
    margin-top: 10px;
  }
  &__element {
    display: block;
    position: relative;
    width: 24%;
    height: 292px;
    float: left;
    overflow: hidden;
    span{
      display: block;
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 17px;
      color: #ffffff;
      text-align: center;
      text-transform: uppercase;
      font-size: 15px;
      z-index: 2;
    }
  }
  &__element:hover{
    .items__img{
      opacity: 0;
    }
    .items__img-hover{
      opacity: 1;
    }
  }
  &__img {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: opacity 500ms ease-in-out;
  }
  &__img-hover {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;;
    object-fit: cover;
    opacity: 0;
    transition: opacity 500ms ease-in-out;
  }
}