.sofa-banner{
  display: block;
  position: relative;
  width: 100%;
  float: left;
  margin-top: -104px;
  z-index: 2;
  &__wrap{
    width: 95%;
    max-width: 1200px;
    min-width: 300px;
    margin-left: auto;
    margin-right: auto;
    text-align: inherit;
    a{
      display: block;
      position: relative;
      width: 100%;
      float: left;
      img{
        width: 100%;
      }
    }
  }
}