.header {
  display: block;
  position: relative;
  width: 100%;
  float: left;
  padding-top: 31px;
  font-family: $gilsansmtregular;
  &__secondary-nav {
    background: #73c0c1;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &__wrap {
    width: 95%;
    max-width: 1200px;
    min-width: 300px;
    margin-left: auto;
    margin-right: auto;
    text-align: inherit;
    &--nav{
      width: 90%;
    }
  }
  .call-bar-link{
    display: none;
    position: relative;
    width: 100%;
    float: left;
    padding: 5px 0px;
    color: white;
    font-size: 20px;
  }
  .secondary-site-nav {
    list-style: none;
    display: block;
    padding: 4px 0 6px 0;
    margin: 0;
    &__item {
      display: inline-block;
      margin-right: 0.75em;
      vertical-align: middle;
      color: #fff;
    }
    &__link {
      color: #fff;
      font-size: 14px;
      &:hover {
        text-decoration: underline;
      }
    }
    &__link:after {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 5px;
      height: 5px;
      border-radius: 5px;
      background: #fff;
      margin-left: 18px;
    }
    &__item:last-of-type {
      a:after {
        display: none;
      }
    }
  }
  &__head {
    display: flex;
    flex-wrap: wrap;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 20px 30px;
    &--left {
      flex: 1;
      display: block;
      position: relative;
     /* width: 30%;*/
      text-align: left;
      .header-banner {
        display: inline-block;
        img {
          max-width: 100%;
          height: auto;
        }
      }
      .header-nav-toggle {
        display: none;
      }
      .header-nav-button {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        padding: 14px;
        border-radius: 99px;
        border: 1px solid #dfdfdf;
        transition: border 250ms ease-in-out;
        .icon-list {
          display: block;
          position: relative;
          width: 18px;
          height: 18px;
          margin: 5px 0px;
        }
        .icon-list-close {
          display: none;
          position: relative;
          width: 18px;
          height: 18px;
          margin: 5px 0px;
        }
      }
      .header-nav-button:hover {
        border-color: #000;
      }
    }
    &--right {
      flex: 1;
      display: block;
      position: relative;
     /* width: 30%;*/
      text-align: right;
    }
    &--logo {
      flex: 1;
      display: block;
      position: relative;
      /*width: 30%;*/
      padding: 0 20px;
      .head-logo {
        display: block;
        position: relative;
        width: 141px;
        height: 111px;
        margin: 0px auto;
        @include overflow-img;
        img {
          max-width: 100%;
          height: 111px;
          object-fit: cover;
        }
      }
    }
  }
  &__call{
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0;
    margin-right: 15px;
    color: black;
    margin-top: -48px;
    &--title{
      display: block;
      color: #747474;
      font-size: 15px;
      margin-bottom: 3px;
    }
    &--link{
      display: block;
      text-transform: uppercase;
      color: #000;
      font-family: $gilsansmtregular;
      line-height: 1.2;
      font-size: 18px;
    }
  }
  &__button-icon{
    display: inline-block;
    position: relative;
    width: 30px;
    height: 30px;
    padding: 14px;
    border-radius: 50%;
    margin-right: 8px;
    border: 1px solid #dfdfdf;
    -webkit-transition: border 250ms ease-in-out;
    transition: border 250ms ease-in-out;
    &:hover{
      border-color: #000;
      .header__heart-icon{
        background-image: url("../img/icons/heart_icon.svg");
      }
      .header__basket-icon{
        background-image: url("../img/icons/shopping-cart_hover_icon.svg");
      }
    }
  }
  &__heart-icon{
    display: block;
    position: relative;
    margin: 5px auto;
    width: 23px;
    height: 23px;
    background-image: url("../img/icons/heart_hover_icon.svg");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    transition: background 250ms ease-in-out;
  }
  &__basket-icon{
    display: block;
    position: relative;
    margin: 5px auto;
    width: 23px;
    height: 23px;
    background-image: url("../img/icons/shopping-cart_icon.svg");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    transition: background 250ms ease-in-out;
  }
  &__primary-nav{
    position: relative;
    width: 100%;
    height: 50px;
    float: left;
    border-top: 1px solid #dfdfdf;
    border-bottom: 1px solid #dfdfdf;
    background: #f6f6f6;
    z-index: 100;
  }
  .primary-site-nav{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    &__item{
      display: block;
      transition: opacity 250ms ease-in-out;
      a{
        display: block;
        position: relative;
        text-transform: uppercase;
        color: #000;
        font-family: "Calibri Light";
        font-size: 16px;
        padding: 13px 5px;
        margin-bottom: 0;
      }
      .bag{
        color: #e92171;
      }
    }
    /*&__item:last-of-type{
      display: block;
      width: 0px;
      overflow: hidden;
      transition: width 250ms ease-in-out;;
    }*/
    &__search{
      display: block;
      position: relative;
      width: 19px;
      height: 24px;
      float: left;
      padding: 13px 0px;
      margin-right: 50px;
      .icon-search{
        display: block;
        position: relative;
        width: 19px;
        height: 24px;
        background-image: url("../img/icons/search_icon.svg");
        background-repeat: no-repeat;
        -webkit-background-size: contain;
        background-size: contain;
        transform: rotate(74deg);
      }
    }
    &__basket{
      display: block;
      position: relative;
      width: 0px;
      height: 24px;
      float: left;
      padding: 13px 0px;
      overflow: hidden;
      transition: width 250ms ease-in-out;
      .icon-basket{
        display: block;
        position: relative;
        width: 23px;
        height: 23px;
        background-image: url("../img/icons/shopping-cart_icon.svg");
        background-repeat: no-repeat;
        -webkit-background-size: cover;
        background-size: cover;
      }
    }
  }
  .primary-nav__links{
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 169px;
  }
  .submenu{
    display: block;
    position: absolute;
    width: 100%;
    height: 0px;
    overflow: hidden;
    top: 100%;
    left: 0;
    margin-top: 1px;
    background-color: #fff;
    padding: 0px;
    transition: height 250ms cubic-bezier(0.24, 0.63, 0.44, 1);
    margin: 0;
    border-bottom: 1px solid #dfdfdf;
    li:nth-of-type(1){
      display: inline-flex;
      flex-wrap: wrap;
      flex-direction: column;
      position: relative;
      width: 30%;
      text-align: left;
      padding-right: 30px;
      padding-left: 70px;
      ul{
        column-count: 2;
        column-gap: 20px;
        width: 100%;
        padding: 0;
        li{
          display: block;
          width: 100%!important;
          float: left;
          padding: 0!important;
          a{
            display: block;
            padding: 3px 0;
            text-transform: inherit;
            border: none;
          }
        }
      }
    }
    li{
      display: inline-flex;
      flex-wrap: wrap;
      flex-direction: column;
      position: relative;
      width: 16%;
      text-align: left;
      padding-right: 30px;
      a{
        border-bottom: 1px solid #dfdfdf;
      }
      ul{
        padding: 0;
        width: 100%;
        column-count: 1;
        column-gap: 20px;
        li{
          display: block;
          width: 100%!important;
          float: left;
          padding: 0!important;
          a{
            display: block;
            padding: 3px 0;
            font-size: 14px;
            text-transform: inherit;
            border: none;
          }
        }
      }
    }
    &__wrap{
      width: 95%;
      max-width: 1200px;
      min-width: 300px;
      margin-left: auto;
      margin-right: auto;
      text-align: inherit;
    }
  }
  &__search-wrap{
    display: block;
    position: relative;
    width: 100%;
    height: 0;
    background-color: #fff;
    border-bottom: 1px solid #dfdfdf;
    overflow: hidden;
    transition: height 250ms cubic-bezier(0.24, 0.63, 0.44, 1);
    float: left;
    z-index: 100;
    margin-top: 3px;
    margin-top: 3px;
  }
  &__search-field{
    display: block;
    width: 100%;
    border: transparent;
    background: transparent;
    font-size: 35px;
    font-weight: 200;
    color: black;
    padding: 30px 0;
  }
}