.main-slider{
  display: block;
  position: relative;
  width: 100%;
  float: left;
  z-index: 1;
  .slider-wrap{
    width: 95%;
    max-width: 1200px;
    min-width: 300px;
    margin-left: auto;
    margin-right: auto;
    text-align: inherit;
  }
  &__notification{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    position: relative;
    width: 100%;
    padding: 10px 0px;
    &--img{
      display: block;
      position: relative;
      width: auto;
      height: 39px;
      float: left;
      margin-right: 10px;
    }
    &--title{
      display: block;
      position: relative;
      float: left;
      color: #747474;
      font-size: 11px;
      line-height: 33px;
    }
    &--link{
      color: #747474;
      text-decoration: underline;
      transition: color 300ms ease-out;
      &:hover{
        color: #73c0c1;
        text-decoration: underline;
      }
    }
  }
  &__box{
    display: block;
    position: relative;
    width: 100%;
    float: left;
  }
  &__item{
    display: block;
    position: relative;
    width: 100%;
    float: left;
    &--img{
      display: block;
      position: relative;
      width: 100%;
      height: 433px;
      @include overflow-img;
      img{
        min-width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &--content{
      display: block;
      position: relative;
      width: 95%;
      max-width: 1200px;
      min-width: 300px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      top: -179px;
      z-index: 2;
    }
  }
  &__slide-title{
    display: block;
    position: relative;
    width: 100%;
    float: left;
    color: #ffffff;
    text-transform: uppercase;
    font-family: $gilsansmtregular;
    font-size: 35px;
    margin: 0;
  }
  &__slide-subtitle{
    display: block;
    position: relative;
    width: 100%;
    float: left;
    color: #ffffff;
    font-family: $gilsansmtregular;
    font-size: 15px;
  }
  &__slide-link{
    display: inline-block;
    position: relative;
    width: 129px;
    height: 19px;
    border-radius: 4px;
    background-color: #73c0c1;
    color: #fff;
    text-transform: uppercase;
    font-family: "Calibri Light";
    font-size: 15px;
    padding: 10px 10px;
    transition: all 250ms ease-in-out;
    &:hover{
      background-color: #50b0b1;
    }
  }
  .owl-controls{
    display: block;
    position: absolute;
    width: 100%;
    bottom: 154px;
    .owl-dots{
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      .owl-dot{
        display: inline-block;
        width: 8px;
        height: 8px;
        background-color: rgba(0,0,0,0.3);
        border-radius: 50%;
        margin: 0px 5px;
      }
      .active{
        display: inline-block;
        width: 8px;
        height: 8px;
        background-color: #73c0c1;
        border-radius: 50%;
      }
    }
  }
}